/* ÏãúÏ¶å Ïπ¥Îìú Ïä§ÌÉÄÏùº */
.season-gallery {
  padding-bottom: 390px; /* ÌéòÏù¥ÏßÄ ÌïòÎã®Ïóê Ï∂©Î∂ÑÌïú Í≥µÍ∞ÑÏùÑ ÌôïÎ≥¥ */
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  margin: 20px 0;
}

.season-card-horizontal {
  display: flex;
  cursor: pointer;
  margin: 15px; /* Ïπ¥Îìú Í∞ÑÍ≤©ÏùÑ ÏïΩÍ∞Ñ Îçî ÎÑìÌûò */
  border: 2px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
  transition: box-shadow 0.3s ease-in-out;
  width: 100%;
  max-width: 800px;
}

.season-card-horizontal:hover {
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.season-img-wrapper {
  width: 33%; /* Ïù¥ÎØ∏ÏßÄ ÏòÅÏó≠ ÌÅ¨Í∏∞ */
  height: 100%;
}

.season-img-wrapper img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.season-info {
  width: 67%; /* ÎÇ¥Ïö© ÏòÅÏó≠ ÌÅ¨Í∏∞ */
  padding: 10px;
  text-align: left;
}

.season-info h3 {
  font-size: 1.5rem;
  margin-bottom: 10px;
  color: #333;
}

.season-info p {
  font-size: 1rem;
  margin-bottom: 20px;
  color: #666;
}

.season-info .verse {
  font-size: 0.9rem;
  color: #555;
  font-style: italic;
}

/* Í∞§Îü¨Î¶¨ ÌåùÏóÖ */
.gallery-popup {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1000;
}

.gallery-content {
  position: relative;
  background: white;
  padding: 20px;
  border-radius: 10px;
  max-width: 90%;
  max-height: 90%;
  overflow: hidden;
  text-align: center;
}

.main-gallery-img {
  width: 100%;
  height: auto;
  max-width: 500px; /* ÏµúÎåÄ ÌÅ¨Í∏∞ Ï†úÌïú */
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.thumbnail-list {
  display: flex;
  justify-content: center;
  margin-top: 10px;
  gap: 10px;
  flex-wrap: wrap;
}

.thumbnail-img {
  width: 60px;
  height: 60px;
  margin: 0 5px;
  cursor: pointer;
  border-radius: 8px;
  object-fit: cover;
  transition: opacity 0.3s ease, transform 0.3s ease;
}

.thumbnail-img:hover {
  opacity: 0.7;
  transform: scale(1.1);
}

.thumbnail-img.active {
  border: 3px solid #4CAF50;
}

.close {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 30px;
  cursor: pointer;
  color: white;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 5px 10px;
  border-radius: 50%;
}

/* Ïù¥Ï†Ñ/Îã§Ïùå Î≤ÑÌäº */
.pagination {
  position: relative;
  bottom: 30px; /* ÌéòÏù¥ÏßÄ Î≤ÑÌäºÏùÑ ÏïÑÎûòÏ™ΩÏúºÎ°ú Ï¢Ä Îçî ÎÇ¥Î¶º */
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  gap: 20px;
  align-items: center;
}

.pagination-buttons {
  position: relative;
  bottom: 80px; /* ÌéòÏù¥ÏßÄ Î≤àÌò∏ Î≤ÑÌäºÏùÑ Ïù¥Ï†Ñ/Îã§Ïùå Î≤ÑÌäº ÏúÑÏóê ÏúÑÏπòÏãúÌÇ¥ */
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 10px;
}

.pagination button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.pagination button:hover:not(:disabled) {
  background-color: #f1f1f1;
}

/* ÌéòÏù¥ÏßÄÎÑ§Ïù¥ÏÖò Î≤ÑÌäº (Î™©Ï∞®) */
.pagination-buttons {
  position: absolute;
  bottom: 80px; /* ÌéòÏù¥ÏßÄ Î≤àÌò∏ Î≤ÑÌäºÏùÑ Ïù¥Ï†Ñ/Îã§Ïùå Î≤ÑÌäº ÏúÑÏóê ÏúÑÏπòÏãúÌÇ¥ */
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 10px;
}

.pagination-buttons button {
  padding: 5px 10px;
  background-color: #fff;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.pagination-buttons button:hover {
  background-color: #f1f1f1;
}
