.sheet-section {
    text-align: center;
    padding: 20px;
    padding-bottom: 300px; /* Í∏∞Ï°¥ 200pxÏóêÏÑú 300pxÎ°ú Ïó¨Ïú†Í≥µÍ∞Ñ Ï∂îÍ∞Ä */
}
  
  .sheet-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 20px;
    justify-items: center;
  }
  
  .sheet-card {
    background: #fff;
    border-radius: 10px;
    padding: 15px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    text-align: center;
    max-width: 250px;
  }
  
  .sheet-card img {
    width: 100%;
    max-height: 150px;
    object-fit: cover;
    border-radius: 10px;
  }
  
  .sheet-card h3,
  .sheet-card h4 {
    margin: 10px 0;
  }
  
  .sheet-card .btn {
    background-color: #007BFF;
    color: white;
    padding: 10px;
    text-decoration: none;
    border-radius: 5px;
    display: inline-flex;
    align-items: center;
  }
  
  .sheet-card .btn:hover {
    background-color: #0056b3;
  }
  
  .pagination {
    position: absolute;
    bottom: 30px; /* ÌéòÏù¥ÏßÄ Î≤ÑÌäºÏùÑ ÏïÑÎûòÏ™ΩÏúºÎ°ú Ï¢Ä Îçî ÎÇ¥Î¶º */
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: center;
    gap: 20px;
    align-items: center;
}

.pagination button {
  background-color: #fff;
  color: #333;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.pagination button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.pagination button:hover:not(:disabled) {
  background-color: #f1f1f1;
}

/* ÌéòÏù¥ÏßÄÎÑ§Ïù¥ÏÖò Î≤ÑÌäº (Î™©Ï∞®) */
.pagination-buttons {
  position: absolute;
  bottom: 80px; /* ÌéòÏù¥ÏßÄ Î≤àÌò∏ Î≤ÑÌäºÏùÑ Ïù¥Ï†Ñ/Îã§Ïùå Î≤ÑÌäº ÏúÑÏóê ÏúÑÏπòÏãúÌÇ¥ */
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 10px;
}

.pagination-buttons button {
  padding: 5px 10px;
  background-color: #fff;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.pagination-buttons button:hover {
  background-color: #f1f1f1;
}